/* Estilos generales */
.App {
  font-family: Arial, sans-serif;
  padding: 20px;
  max-width: 600px;
  margin: auto;
  background-color: #ffffff;
}

.title {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
  font-size: 24px;
}

.add-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.input-field {
  width: 75%;
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.add-button {
  width: 20%;
  padding: 8px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
}

.add-button:hover {
  background-color: #218838;
}

.item-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.item {
  display: flex;
  align-items: center;
  padding: 8px 0;
  margin-bottom: 10px;
}

.checkbox {
  margin-right: 10px;
}

.name-input {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
  margin-right: 10px;
  width: 50%; /* El nombre del ítem ahora ocupa más espacio */
  min-width: 150px; /* Asegura que se vea bien incluso en pantallas pequeñas */
  white-space: nowrap; /* Evita que se corte el texto */
  overflow: hidden; /* Evita que se desborde el contenido */
  text-overflow: ellipsis; /* Muestra puntos suspensivos si el texto es demasiado largo */
}

.quantity-input {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
  width: 15%; /* Reducido tamaño del campo de cantidad */
  min-width: 80px; /* Evita que el campo se haga demasiado pequeño */
}

.delete-button {
  background-color: transparent;
  border: none;
  color: #e74c3c;
  font-size: 20px;
  cursor: pointer;
}

.delete-button:hover {
  color: #c0392b;
}

.send-button {
  width: 100%;
  padding: 12px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}

.send-button:hover {
  background-color: #0056b3;
}
